// title, src, alt, projects [title, image, desc, tags, repo, page]
import pseudoDB from "../pseudoDB.json";
import Project from "./Project";

const hidden = {
  display: "none",
};

const visible = {
  display: "flex",
};

export default function PopupCat(props) {
  return (
    <div id="popup-window" style={props.popup}>
      <div
        id="popup-content"
        className="animate"
        style={(props.popup, { backgroundColor: props.color })}
      >
        <span id="close">&times;</span>
        <div className="popup-projects">
          <h1 className="category-title">{props.category}</h1>
          {pseudoDB
            .filter((item) => item.title === props.category)
            .map((item) =>
              item.projects.map((project) => (
                <Project project={project} color={props.color} />
              ))
            )}
        </div>
      </div>
    </div>
  );
}
