import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTags } from "@fortawesome/free-solid-svg-icons";
import {
  faHtml5,
  faSass,
  faCss3Alt,
  faJsSquare,
  faJs,
  faReact,
} from "@fortawesome/free-brands-svg-icons";

export default function Tags(props) {
  return (
    <ul className="tags">
      {props.tags.map((tag) => {
        let tagIcon = faTags;
        switch (tag) {
          case "html":
            tagIcon = faHtml5;
            break;
          case "css":
            tagIcon = faCss3Alt;
            break;
          case "scss":
            tagIcon = faSass;
            break;
          case "react":
            tagIcon = faReact;
            break;
          case "js":
            tagIcon = faJsSquare;
            break;
          case "codehs":
            tagIcon = faJs;
            break;
        }
        return (
          <li className="tag" title={tag}>
            <FontAwesomeIcon icon={tagIcon} />
            <span> {tag.toUpperCase()}</span>
          </li>
        );
      })}
    </ul>
  );
}
